/* Extra small devices (phones, 400px and down) */
@media only screen and (max-width: 250px) {
    div.leaflet-popup-content-wrapper {
        width: 200px;
    }
    
    div.popupInfoWrapperDiv {
        width: 180px;
    }
}

/* Extra small devices (phones, 400px and down) */
@media only screen and (min-device-width: 251px) and (max-width: 300px) {
    div.leaflet-popup-content-wrapper {
        width: 240px;
    }
    
    div.popupInfoWrapperDiv {
        width: 220px;
    }
}

/* Extra small devices (phones, 400px and down) */
@media only screen and (min-device-width: 301px) and (max-width: 400px) {
    div.leaflet-popup-content-wrapper {
        width: 280px;
    }
    
    div.popupInfoWrapperDiv {
        width: 250px;
    }
}

/* Small devices (portrait tablets and large phones, 600px and up) */
@media only screen and (min-device-width: 401px) and (min-width: 500px) {
    div.leaflet-popup-content-wrapper {
        width: 380px;
    }
    
    div.popupInfoWrapperDiv {
        width: 350px;
    }
}

/* Small devices (portrait tablets and large phones, 600px and up) */
@media only screen and (min-device-width: 501px) and (min-width: 600px) {
    div.leaflet-popup-content-wrapper {
        width: 480px;
    }
    
    div.popupInfoWrapperDiv {
        width: 450px;
    }
}

/* Medium devices (landscape tablets, 768px and up) */
@media only screen and (min-device-width: 601px) and (min-width: 768px) {
    div.leaflet-popup-content-wrapper {
        width: 700px;
    }
    
    div.popupInfoWrapperDiv {
        width: 660px;
    }
}

/* Large devices (laptops/desktops, 992px and up) */
@media only screen and (min-device-width: 769px) and (min-width: 992px) {
    div.leaflet-popup-content-wrapper {
        width: 700px;
    }
    
    div.popupInfoWrapperDiv {
        width: 670px;
    }
}

/* Extra large devices (large laptops and desktops, 1200px and up) */
@media only screen and (min-device-width: 993px) {
    div.leaflet-popup-content-wrapper {
        width: 700px;
    }

    
    div.popupInfoWrapperDiv {
        width: 670px;
    }

    
}